@use "../partials/" as vars;
@use "../mixins/" as mix;

.card-section {
	width: 100%;
	height: max-content;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	justify-items: center;
	gap: 10px;
	background-color: vars.$clr-nav;
	padding-bottom: 25px;

	@include mix.media(1031px) {
		grid-template-columns: repeat(3, 1fr);
	}

	@include mix.media(968px) {
		grid-template-columns: repeat(2, 1fr);
	}
}

.card-section-title {
	grid-column: 1 / -1;
	width: 100%;
	height: 125px;
	text-align: center;
	margin: 20px 0 15px 0;
}

.card-section-h3 {
	margin-top: 48px;
	font-size: large;
}

.card-section-body {
	grid-column: 1 / -1;
	width: 80%;
	height: 150px;
	text-align: center;
	margin: 10px 0 30px 0;
	background-color: black;
	border-radius: 10px;
	@include mix.media(1031px) {
		grid-column: 1 / -1;
		width: 80%;
	}

	@include mix.media(968px) {
		grid-column: 1 / -1;
		width: 80%;
	}
}

.card-section-body > p {
	text-transform: capitalize;
	margin-top: 10px;
	padding: 5px;
	color: white;
}


.card-wrappers{
	margin: 5px;
	width: 90%;
	max-height: 100%;

	grid-column:  span 1;

	@include mix.media(1031px) {
		grid-column:span 1;
		width: 100%;
	}

	@include mix.media(968px) {
		grid-column:  1 / -1;
		width: 35vw;
	}
}


.cards {
	border-radius: 15px;

	width: 100%;
	max-height: 100%;

	background-color: vars.$clr-text;

}


.cards > h2 {
	text-align: center;
	background-color: white;
	border-radius: 15px 15px 0 0;
}


.card {
	width: 100%;
	height: 100%;
	background-color: vars.$clr-body;
	border-radius: 0 0 15px 15px;
	text-align: center;
}

.card-text {
	padding: 10px;
	color: vars.$clr-text;
}

.card-img {
	width: 100%;
	height: 100%;
	border-radius: 0 0 15px 15px ;

	background-color: vars.$clr-text;
	justify-content: center;

	//	padding: 20px;
}

.img-card {
	background-color: white;
	width: 150px;
	height: 150px;
	margin: 15px 0 15px 0;
	padding: 15px;

	border-radius: 10px;
}
